.btn {
  display: block;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  font-weight: 700;
  padding: 1.5rem 3rem;
  font-size: 1.2rem;
  line-height: 1.5rem;
  background-color: transparent;
  color: #ffffff;
  border: 2px solid #eeb662;
  border-radius: 2.8rem / 2.8rem;
  text-decoration: none;
  transition: all 0.3s;
}
.btn:hover {
  color: #ffffff;
  background: #eeb662;
}
.btn:active {
  background: #eeb662;
}
.btn--square {
  display: block;
  text-align: center;
  white-space: nowrap;
