  background-color: transparent;
  color: #ffffff;
  border: 2px solid #eeb662;
  border-radius: 2.8rem / 2.8rem;
  text-decoration: none;
  transition: all 0.3s;
  border-radius: 3px;
  background-color: #eeb662;
  text-transform: uppercase;
  width: 24rem;
  font-size: 1.4rem;
  letter-spacing: 0.2em;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
  padding: 2rem 3rem;
}
.btn--square:hover {
  color: #ffffff;
  background: #eeb662;
}
