.rocks__image {
  position: relative;
  display: block;
  width: 100%;
}
.rocks__name {
  display: block;
  margin-top: 1em;
  line-height: 1.1em;
}
.rocks__zoom {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.rocks__zoom:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 4.25rem;
  height: 4.25rem;
  right: 0;
  left: 0;
  margin: auto auto;
  background: url(../img/icon_zoom.png) no-repeat 50% 50%;
  background-size: contain;
  z-index: 10;
  transform: scale(0.8);
  opacity: 0;
  transition: .4s ease;
}
.rocks__zoom:hover:before {
  transform: scale(1);
  opacity: 1;
}
.rocks__zoom:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: #eeb662;
  border-radius: 1000px;
  opacity: 0;
  transition: .4s ease;
}
.rocks__zoom:hover:after {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0;
  opacity: .9;
}
